import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { blueGrey, pink, teal, indigo } from '@material-ui/core/colors';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import { blue } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 440,
    backgroundColor: theme.palette.background.paper,
  },
  avatar: {
    backgroundColor: blue[100],
    color: blue[600],
  },
}));

const tempColor = teal["400"];
const humidityColor = indigo["400"];
const uvColor = blueGrey["400"];
const lumColor = pink["400"];

const getColor = (value, type) => {
  switch (type) {
    case 'temp': return tempColor;
    case 'humidity': return humidityColor;
    case 'uv': return uvColor;
    case 'lum': return lumColor;
  }
}

const getUnitSign = (type) => {
  switch (type) {
    case "temp": return "C";
    case "humidity": return "%";
    case "lum": return "L";
    case "uv": return "UV";
    default: return "N/A";
  }
}

const epochToDateTime = (epoch) => {
  const d = new Date(0);
  d.setUTCSeconds(epoch / 1000);
  return d.toLocaleDateString('sv-SE') + ' ' + d.toLocaleTimeString();
}

const epochToTime = (epoch) => {
  const d = new Date(0);
  d.setUTCSeconds(epoch / 1000);
  return d.toLocaleTimeString();
}

export default function ValueButton({ sensor }) {

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  return (
    <Button
      variant="contained"
      key={sensor.type}
      style={{ backgroundColor: getColor(sensor.data.slice().reverse()[0][1], sensor.type) }}
      onClick={handleClick}
    >{sensor.data.slice().reverse()[0][1] + getUnitSign(sensor.type)}
      <Dialog onClose={handleClick} aria-labelledby="simple-dialog-title" open={open}>
        <DialogTitle id="simple-dialog-title">{sensor.name + ' (' + sensor.type + ')'}</DialogTitle>
        <List>
          {sensor.data.map((entry) => (
            <ListItem button onClick={handleClick}>
              <ListItemText primary={epochToTime(entry[0])} />
              <ListItemText primary={entry[1]} />
            </ListItem>
          ))}
        </List>
      </Dialog>
    </Button>
  )
}