import React from 'react';
import SensorScreen from './SensorScreen';
import CssBaseline from '@material-ui/core/CssBaseline';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import { makeStyles } from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

const reloadData = (setNotifictionEvents) => {
  console.log('reloadData');
  fetch(`https://aoq53tdls8.execute-api.us-east-1.amazonaws.com/dev/sensorNotificationEvent`)
  .then(response => response.json())
  .then(json => {
    setNotifictionEvents(json);
  })
  .catch((error) => {
    console.error('ERROR',error);
  });
}

const epochToTime = (epoch) => {
  const d = new Date(0);
  d.setUTCSeconds(epoch / 1000);
  return d.toLocaleTimeString();
}

export default function App() {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const [openEvents, setOpenEvents] = React.useState(false);
  const [notifictionEvents, setNotifictionEvents] = React.useState([]);

  const handleClickEvents = () => {
    setOpenEvents(false);
  };

  const handleOpenEvents = () => {
    setOpenEvents(true);
  };

  React.useEffect(() => {
    reloadData(setNotifictionEvents);
  }, []);

  return (
    <React.Fragment>
      <CssBaseline />
      <div className={classes.root}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton 
            edge="start" 
            className={classes.menuButton} 
            color="inherit" 
            aria-label="menu" 
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            open={open}
            onClose={handleClose}
          >
            <MenuItem onClick={handleClose}>Profile</MenuItem>
            <MenuItem onClick={handleOpenEvents}>
              Notification Events
              <Dialog onClose={handleClickEvents} aria-labelledby="simple-dialog-title" open={openEvents}>
                <DialogTitle id="simple-dialog-title">Notification Events</DialogTitle>
                <List>
                  {notifictionEvents.map((entry) => (
                    <ListItem button onClick={handleClickEvents}>
                      <ListItemText primary={epochToTime(entry.timestamp)} />
                      <ListItemText primary={entry.name} />
                      <ListItemText primary={entry.message} />
                    </ListItem>
                  ))}
                </List>
              </Dialog>
            </MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>
      <Toolbar/>
      </div>
      <SensorScreen />
    </React.Fragment>
  );
}


